import React from "react";
import { Link } from "react-router-dom";
import { prod } from "../../App";

const Header = () => {
  return (
    <div className="row page-titles">
      <div className="col-md-5 align-self-center">
        <h4 className="text-themecolor">Real Estate Dashboard</h4>
      </div>
      <div className="col-md-7 align-self-center text-right">
        <div className="d-flex justify-content-end align-items-center">
          <ol className="breadcrumb">
            <li className="breadcrumb-item">
              <a href="#">Home</a>
            </li>
            <li className="breadcrumb-item active">Real Estate Dashboard</li>
          </ol>
          <Link to={prod ? "/dashboard/add-new-property" : "/add-new-property"}>
            <button
              type="button"
              className="btn btn-info d-none d-lg-block m-l-15"
            >
              <i className="fa fa-plus-circle"></i> Create New
            </button>
          </Link>
        </div>
      </div>
    </div>
  );
};

export default Header;
