import React, { useState } from "react";
import { Link } from "react-router-dom";

import Loading from "../../../../shared/loading/Loading";

import Drawer from "react-drag-drawer";

import "./styles/PopupAlert.css";
import { link, prod } from "../../../../App";

const PropertyTableElement = ({
  url,
  _id,
  title,
  basic,
  expirationDay,
  mainImage,
}) => {
  const [loadingIcon, setLoadingIcon] = useState(false);
  const [modalState, setModalState] = useState(false);

  const handleDelete = () => {
    setLoadingIcon(true);
    fetch(`${link}/api/v1/properties/${_id}`, {
      method: "DELETE",
    }).then(() => {
      window.location.reload();
    });
  };

  const toggle = () => {
    setModalState(!modalState);
  };
  return (
    <tr>
      <Drawer open={modalState} onRequestClose={toggle}>
        <div className="popup">
          {loadingIcon ? (
            <Loading />
          ) : (
            <>
              <p>
                Are you sure to delete Property, It is permanatly delete
                proporty!
              </p>
              <button
                type="button"
                className="btn btn-danger"
                role="menuitem"
                onClick={handleDelete}
              >
                DELETE
              </button>
              <button
                type="button"
                className="btn btn-warning m-l-15"
                role="menuitem"
                onClick={() => setModalState(false)}
              >
                Cancel
              </button>
            </>
          )}
        </div>
      </Drawer>
      <td>{title}</td>
      <td>
        <img
          src={`${link}/img/properties/${mainImage}`}
          alt={title}
          width="80"
        />{" "}
      </td>
      <td>{basic.propertyType}</td>
      <td>{expirationDay.slice(0, 10)}</td>
      <td>
        <span
          className={
            basic.status === "rent"
              ? "label label-success font-weight-100"
              : "label label-warning font-weight-100"
          }
        >
          {basic.status}
        </span>
      </td>
      <td>
        <Link
          className="text-dark p-r-10"
          to={prod ? `/dashboard/edit/${_id}` : `/edit/${_id}`}
        >
          <i className="ti-marker-alt"></i>
        </Link>
        <span
          className="text-dark"
          title="Delete"
          onClick={() => setModalState(true)}
          style={{ cursor: "pointer" }}
        >
          <i className="ti-trash"></i>
        </span>
      </td>
    </tr>
  );
};

export default PropertyTableElement;
