import React from "react";

import logoIcon from "../../assets/images/logo-icon.png";
import logoLightIcon from "../../assets/images/logo-light-icon.png";

import logoText from "../../assets/images/logo-text.png";
import logoLightText from "../../assets/images/logo-light-text.png";

import useWindowSize from "../hooks/useWindowSizes";

const Topbar = () => {
  const size = useWindowSize();

  return (
    <header className="topbar">
      <nav className="navbar top-navbar navbar-expand-md navbar-dark">
        <div className="navbar-header">
          {size.width > 767 ? (
            <a className="navbar-brand" href="index.html">
              <b>
                <img src={logoIcon} alt="homepage" className="dark-logo" />
                <img
                  src={logoLightIcon}
                  alt="homepage"
                  className="light-logo"
                />
              </b>
              {size.width < 1170 ? null : (
                <span>
                  <img src={logoText} alt="homepage" className="dark-logo" />
                  <img
                    src={logoLightText}
                    className="light-logo"
                    alt="homepage"
                  />
                </span>
              )}
            </a>
          ) : (
            <span
              className="nav-link nav-toggler d-block d-md-none waves-effect waves-dark"
              style={{ padding: ".5rem 0.4rem", fontSize: "40px" }}
            >
              <i className="ti-close ti-menu"></i>
            </span>
          )}
        </div>
        <div className="navbar-collapse">
          <ul className="navbar-nav mr-auto"></ul>
          <ul className="navbar-nav my-lg-0">
            <li className="nav-item m-r-10">
              <form className="app-search d-none d-md-block d-lg-block">
                <input
                  type="text"
                  className="form-control"
                  placeholder="Search & enter"
                />
              </form>
            </li>
          </ul>
        </div>
      </nav>
    </header>
  );
};

export default Topbar;
