import React from "react";

const FormElements = props => {
  const {
    req = false,
    label = "Default Label",
    type = "input",
    inputType = "text",
    name = "default",
    options,
    handleChange,
    state,
    group = ""
  } = props;

  const elements = () => {
    switch (type) {
      case "dropdown":
        return (
          <select
            className={
              req
                ? "custom-select form-control required"
                : "custom-select form-control"
            }
            id={`w${name}`}
            name={name}
            value={state}
            onChange={e => handleChange(name, e.target.value, group)}
          >
            {options.map((option, index) => (
              <option key={index} value={option}>
                {option}
              </option>
            ))}
          </select>
        );
      case "checkbox":
        return (
          <div class="custom-control custom-checkbox mr-sm-2 mb-3">
            <input
              type="checkbox"
              class="custom-control-input"
              id={`c${name}`}
              defaultChecked={state}
              onClick={() => handleChange(name, !state, group)}
            />
            <label class="custom-control-label" for={`c${name}`}>
              {name}
            </label>
          </div>
        );
      default:
        return (
          <input
            type={inputType}
            className={req ? "form-control required" : "form-control"}
            id={`w${name}`}
            name={name}
            value={state}
            onChange={e => handleChange(name, e.target.value, group)}
          />
        );
    }
  };

  return (
    <div className="form-group">
      {type !== "checkbox" ? (
        <label htmlFor={`w${name}`}>
          {label} : {req && <span className="danger">*</span>}
        </label>
      ) : null}
      {elements()}
    </div>
  );
};

export default FormElements;
