import React, { useState, useEffect } from "react";
import PropertyTableElement from "./components/PropertyTableElement";
import Pagination from "./components/Pagination";
import { link } from "../../../App";

const PropertyTable = () => {
  const [properties, setProperties] = useState([]);
  const [numberOfProperties, setNumberOfProperties] = useState(0);
  const [propertyPerPage, setPropertyPerPage] = useState(4);
  const [pageNumber, setPageNumber] = useState(1);

  useEffect(() => {
    fetch(
      `${link}/api/v1/properties?page=${pageNumber}&limit=${propertyPerPage}&sort=-expirationDay`
    )
      .then((res) => res.json())
      .then((json) => {
        if (json.status === "success") {
          setProperties(json.data.data);
        }
      });
    fetch(`${link}/api/v1/properties/count`)
      .then((res) => res.json())
      .then((json) => {
        if (json.status === "success") {
          setNumberOfProperties(json.count);
        }
      });
  }, []);

  const handlePagination = (page) => {
    let param = "";
    if (page === "next") {
      param = `?page=${pageNumber + 1}&limit=${propertyPerPage}`;
    } else if (page === "previous") {
      param = `?page=${pageNumber - 1}&limit=${propertyPerPage}`;
    } else {
      param = `?page=${page}&limit=${propertyPerPage}`;
    }
    fetch(`${link}/api/v1/properties${param}&sort=-expirationDay`)
      .then((res) => res.json())
      .then((json) => {
        if (json.status === "success") {
          setProperties(json.data.data);
          if (page === "next") {
            setPageNumber(pageNumber + 1);
          } else if (page === "previous") {
            setPageNumber(pageNumber - 1);
          } else {
            setPageNumber(page);
          }
        }
      });
  };
  return (
    <div className="col-lg-12">
      <div className="card">
        <div className="card-body">
          <h5 className="card-title">PROPERTY OVERVIEW</h5>
          <div className="table-responsive">
            <table className="table product-overview">
              <thead>
                <tr>
                  <th>Title</th>
                  <th>Image</th>
                  <th>Property Type</th>
                  <th>Expiration Day</th>
                  <th>Status</th>
                  <th>Actions</th>
                </tr>
              </thead>
              <tbody>
                {properties.map((property, index) => {
                  return <PropertyTableElement {...property} key={index} />;
                })}
              </tbody>
              <tfoot>
                <tr className="footable-paging">
                  {numberOfProperties > 0 && (
                    <Pagination
                      count={numberOfProperties}
                      perPage={propertyPerPage}
                      handlePagination={handlePagination}
                      pageNumber={pageNumber}
                    />
                  )}
                </tr>
              </tfoot>
            </table>
          </div>
        </div>
      </div>
    </div>
  );
};

export default PropertyTable;
