import React from "react";

const Col = ({ children, style, className, md }) => {
  let tempClassName = () => {
    let temp = "";
    if (md) {
      temp += `col-md-${md}`;
    }

    return temp;
  };
  return (
    <div
      className={
        className ? `${tempClassName()} ${className}` : tempClassName()
      }
      style={style}
    >
      {children}
    </div>
  );
};

export default Col;
