import React, { useState } from "react";
import Row from "../../../../shared/html/Row";

import history from "../../../../shared/history/history";

import "./styles/PropertyDetails.css";
import FormElements from "../../../AddNewProperty/components/FormElements/FormElements";
import Loading from "../../../../shared/loading/Loading";
import Drawer from "react-drag-drawer";
import { link } from "../../../../App";

const PropertyDetails = ({
  _id,
  url,
  title,
  basic,
  location,
  mainImage,
  detailedInfo,
}) => {
  const [loadingIcon, setLoadingIcon] = useState(false);

  const [formState, setFormState] = useState({
    url: url,
    title: title,
    basic: {
      status: basic.status,
      propertyType: basic.propertyType,
      price: basic.price,
      sqft: basic.sqft,
      bedrooms: basic.bedrooms,
      bathrooms: basic.bathrooms,
    },
    location: {
      address: location.address,
      city: location.city,
      state: location.state,
      zipCode: location.zipCode,
    },
    detailedInfo: {
      description: detailedInfo.description,
      buildingAge: detailedInfo.buildingAge,
      garage: detailedInfo.garage,
      rooms: detailedInfo.rooms,
      otherFeatures: {
        internet: detailedInfo.otherFeatures.internet,
        beach: detailedInfo.otherFeatures.beach,
        balcony: detailedInfo.otherFeatures.balcony,
        airCondition: detailedInfo.otherFeatures.airCondition,
        terrace: detailedInfo.otherFeatures.terrace,
        heating: detailedInfo.otherFeatures.heating,
        wifi: detailedInfo.otherFeatures.wifi,
        parking: detailedInfo.otherFeatures.parking,
        icon: detailedInfo.otherFeatures.icon,
        smokingAllow: detailedInfo.otherFeatures.smokingAllow,
        microwave: detailedInfo.otherFeatures.microwave,
        bedding: detailedInfo.otherFeatures.bedding,
      },
    },
  });

  const [imageState, setImageState] = useState({
    mainImage: "",
    gallery: [],
  });

  const handleInputChange = (field, value, group) => {
    let tempObj = { ...formState };

    if (group !== "" && group !== "otherFeatures") {
      tempObj[group][field] = value;
    } else if (group === "otherFeatures") {
      tempObj.detailedInfo.otherFeatures[field] = value;
    } else {
      tempObj[field] = value;
    }
    console.error("FORM FIELDS", tempObj);
    setFormState(tempObj);
  };

  const handleFileChange = (file, group) => {
    let tempObj = { ...imageState };

    if (group === "mainImage") {
      tempObj[group] = file;
    } else {
      tempObj[group] = [...file];
    }
    console.error("UPLOADS", tempObj);
    setImageState(tempObj);
  };

  const handleFormSubmit = () => {
    setLoadingIcon(true);
    fetch(`${link}/api/v1/properties/${_id}`, {
      method: "PATCH",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(formState),
    })
      .then((res) => res.json())
      .then((json) => {
        if (json.status === "success") {
          let fetchCondition = false;
          let formData = new FormData();
          if (imageState.mainImage !== "") {
            formData.append("mainImage", imageState.mainImage);
            fetchCondition = true;
          }
          if (imageState.gallery.length > 0) {
            imageState.gallery.map((image) => {
              formData.append("gallery", image);
            });
            fetchCondition = true;
          }
          if (fetchCondition) {
            fetch(`${link}/api/v1/properties/${json.data.data._id}`, {
              method: "PATCH",
              body: formData,
            })
              .then((res) => res.json())
              .then((json) => console.log("FILE UPLOADED", json))
              .catch((err) => console.error(err));
          }
        }
      })
      .then(() => setLoadingIcon(false))
      .catch((err) => console.error(err));
  };

  const handleCancel = () => {
    setFormState({
      url: url,
      title: title,
      basic: {
        status: basic.status,
        propertyType: basic.propertyType,
        price: basic.price,
        sqft: basic.sqft,
        bedrooms: basic.bedrooms,
        bathrooms: basic.bathrooms,
      },
      location: {
        address: location.address,
        city: location.city,
        state: location.state,
        zipCode: location.zipCode,
      },
      detailedInfo: {
        description: detailedInfo.description,
        buildingAge: detailedInfo.buildingAge,
        garage: detailedInfo.garage,
        rooms: detailedInfo.rooms,
        otherFeatures: {
          internet: detailedInfo.otherFeatures.internet,
          beach: detailedInfo.otherFeatures.beach,
          balcony: detailedInfo.otherFeatures.balcony,
          airCondition: detailedInfo.otherFeatures.airCondition,
          terrace: detailedInfo.otherFeatures.terrace,
          heating: detailedInfo.otherFeatures.heating,
          wifi: detailedInfo.otherFeatures.wifi,
          parking: detailedInfo.otherFeatures.parking,
          icon: detailedInfo.otherFeatures.icon,
          smokingAllow: detailedInfo.otherFeatures.smokingAllow,
          microwave: detailedInfo.otherFeatures.microwave,
          bedding: detailedInfo.otherFeatures.bedding,
        },
      },
    });
  };

  //DELETE FUNCTIONS
  const [deleteLoadingIcon, setDeleteLoadingIcon] = useState(false);
  const [modalState, setModalState] = useState(false);

  const handleDelete = () => {
    setDeleteLoadingIcon(true);
    fetch(`${link}/api/v1/properties/${_id}`, {
      method: "DELETE",
    }).then(() => {
      history.push("/");
    });
  };

  const toggle = () => {
    setModalState(!modalState);
  };
  return (
    <Row>
      <div class="col-lg-8 col-md-8 col-sm-12 col-xs-12">
        <div class="card">
          <div class="card-body">
            <div
              id="carouselExampleIndicators2"
              class="carousel slide"
              data-ride="carousel"
            >
              <ol class="carousel-indicators">
                <li
                  data-target="#carouselExampleIndicators2"
                  data-slide-to="0"
                  class="active"
                ></li>
                <li
                  data-target="#carouselExampleIndicators2"
                  data-slide-to="1"
                ></li>
                <li
                  data-target="#carouselExampleIndicators2"
                  data-slide-to="2"
                ></li>
              </ol>
              <div class="carousel-inner" role="listbox">
                <div class="carousel-item active">
                  <img
                    class="img-responsive"
                    src={`${link}/img/properties/${mainImage}`}
                    alt="First slide"
                  />
                </div>
                <div class="carousel-item">
                  <img
                    class="img-responsive"
                    src="../assets/images/property/prop7.jpg"
                    alt="Second slide"
                  />
                </div>
                <div class="carousel-item">
                  <img
                    class="img-responsive"
                    src="../assets/images/property/prop8.jpg"
                    alt="Third slide"
                  />
                </div>
              </div>
              <a
                class="carousel-control-prev"
                href="#carouselExampleIndicators2"
                role="button"
                data-slide="prev"
              >
                <span
                  class="carousel-control-prev-icon"
                  aria-hidden="true"
                ></span>
                <span class="sr-only">Previous</span>
              </a>
              <a
                class="carousel-control-next"
                href="#carouselExampleIndicators2"
                role="button"
                data-slide="next"
              >
                <span
                  class="carousel-control-next-icon"
                  aria-hidden="true"
                ></span>
                <span class="sr-only">Next</span>
              </a>
            </div>
            <div class="p-t-20 p-b-20">
              <h4 class="card-title">
                <FormElements
                  req
                  label="Title"
                  name="title"
                  handleChange={handleInputChange}
                  state={formState.title}
                />
              </h4>
            </div>
            <hr class="m-0" />
            <p class="text-dark p-t-20 pro-desc">
              <FormElements
                label="Description"
                name="description"
                handleChange={handleInputChange}
                state={formState.detailedInfo.description}
                group="detailedInfo"
              />
            </p>
          </div>
        </div>
        <div class="row">
          <div class="col-sm-6">
            <div className="card">
              <div class="card-body">
                <h5 class="card-title">Basics</h5>
                <div class="table-responsive p-t-10 border-top">
                  <table class="table no-border">
                    <tbody class="text-dark">
                      <tr>
                        <FormElements
                          label="Status"
                          type="dropdown"
                          options={["rent", "sale"]}
                          name="status"
                          handleChange={handleInputChange}
                          state={formState.basic.status}
                          group="basic"
                        />
                      </tr>
                      <tr>
                        <FormElements
                          label="Property Type"
                          type="dropdown"
                          options={[
                            "",
                            "Houses",
                            "Apartment",
                            "Villas",
                            "Commercial",
                            "Offices",
                          ]}
                          name="propertyType"
                          handleChange={handleInputChange}
                          state={formState.basic.propertyType}
                          group="basic"
                        />
                      </tr>
                      <tr>
                        <FormElements
                          label="Price"
                          inputType="number"
                          name="price"
                          handleChange={handleInputChange}
                          state={formState.basic.price}
                          group="basic"
                        />
                      </tr>
                      <tr>
                        <FormElements
                          label="Sqft"
                          inputType="number"
                          name="sqft"
                          handleChange={handleInputChange}
                          state={formState.basic.sqft}
                          group="basic"
                        />
                      </tr>
                      <tr>
                        <FormElements
                          label="Bedrooms"
                          inputType="number"
                          name="bedrooms"
                          handleChange={handleInputChange}
                          state={formState.basic.bedrooms}
                          group="basic"
                        />
                      </tr>
                      <tr>
                        <FormElements
                          label="Bathrooms"
                          inputType="number"
                          name="bathrooms"
                          handleChange={handleInputChange}
                          state={formState.basic.bathrooms}
                          group="basic"
                        />
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
          <div class="col-sm-6">
            <div class="card">
              <div class="card-body">
                <h5 class="card-title">Other Features</h5>
                <hr class="m-0 p-10" />
                {Object.keys(formState.detailedInfo.otherFeatures).map(
                  (feature) => {
                    return (
                      <FormElements
                        type="checkbox"
                        name={feature}
                        state={formState.detailedInfo.otherFeatures[feature]}
                        group={"otherFeatures"}
                        handleChange={handleInputChange}
                        label={feature}
                      />
                    );
                  }
                )}
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="col-lg-4 col-md-4 col-sm-12 col-xs-12">
        <div class="card">
          <div class="card-body">
            <h5 class="card-title">Update</h5>
            <div class="table-responsive">
              <table class="table no-border">
                <tbody class="text-dark">
                  <tr>
                    {loadingIcon ? <Loading /> : null}
                    <button
                      type="button"
                      className="btn btn-info m-l-15"
                      onClick={() => handleFormSubmit()}
                      role="menuitem"
                    >
                      Submit
                    </button>
                    <button
                      type="button"
                      className="btn btn-warning m-l-15"
                      onClick={() => handleCancel()}
                      role="menuitem"
                    >
                      Cancel
                    </button>

                    <button
                      type="button"
                      className="btn btn-danger m-l-30"
                      role="menuitem"
                      onClick={() => setModalState(true)}
                    >
                      DELETE
                    </button>
                    <Drawer open={modalState} onRequestClose={toggle}>
                      <div className="popup">
                        {deleteLoadingIcon ? (
                          <Loading />
                        ) : (
                          <>
                            <p>
                              Are you sure to delete Property, It is permanatly
                              delete proporty!
                            </p>
                            <button
                              type="button"
                              className="btn btn-danger"
                              role="menuitem"
                              onClick={handleDelete}
                            >
                              DELETE
                            </button>
                            <button
                              type="button"
                              className="btn btn-warning m-l-15"
                              role="menuitem"
                              onClick={() => setModalState(false)}
                            >
                              Cancel
                            </button>
                          </>
                        )}
                      </div>
                    </Drawer>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>

        <div class="card">
          <div class="card-body">
            <h5 class="card-title">Details</h5>
            <div class="table-responsive p-t-10 border-top">
              <table class="table no-border">
                <tbody class="text-dark">
                  <tr>
                    <FormElements
                      label="Building Age"
                      name="buildingAge"
                      inputType="number"
                      handleChange={handleInputChange}
                      state={formState.detailedInfo.buildingAge}
                      group="detailedInfo"
                    />
                  </tr>
                  <tr>
                    <FormElements
                      label="Garage"
                      name="garage"
                      inputType="number"
                      handleChange={handleInputChange}
                      state={formState.detailedInfo.garage}
                      group="detailedInfo"
                    />
                  </tr>
                  <tr>
                    <FormElements
                      label="Rooms"
                      name="rooms"
                      inputType="number"
                      handleChange={handleInputChange}
                      state={formState.detailedInfo.rooms}
                      group="detailedInfo"
                    />
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
        <div class="card">
          <div class="card-body">
            <h5 class="card-title">Location</h5>
            <div class="table-responsive p-t-10 border-top">
              <table class="table no-border">
                <tbody class="text-dark">
                  <tr>
                    <FormElements
                      req
                      label="Address"
                      name="address"
                      handleChange={handleInputChange}
                      state={formState.location.address}
                      group="location"
                    />
                  </tr>
                  <tr>
                    <FormElements
                      label="City"
                      name="city"
                      handleChange={handleInputChange}
                      state={formState.location.city}
                      group="location"
                    />
                  </tr>
                  <tr>
                    <FormElements
                      label="State"
                      name="state"
                      handleChange={handleInputChange}
                      state={formState.location.state}
                      group="location"
                    />
                  </tr>
                  <tr>
                    <FormElements
                      label="Zip Code"
                      name="zipCode"
                      handleChange={handleInputChange}
                      state={formState.location.zipCode}
                      group="location"
                    />
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
        <div class="card">
          <div class="card-body">
            <h5 class="card-title">Url</h5>
            <div class="table-responsive">
              <table class="table no-border">
                <tbody class="text-dark">
                  <tr>
                    <FormElements
                      label="Url"
                      name="url"
                      handleChange={handleInputChange}
                      state={formState.url}
                    />
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </Row>
  );
};

export default PropertyDetails;
