import React from "react";
import { Route, Router, Switch } from "react-router-dom";

import history from "./shared/history/history";

import Navigation from "./shared/navigation/Navigation";
import Topbar from "./shared/topbar/Topbar";

import Dashboard from "./Dashboard/Dashboard";
import Inbox from "./Inbox/Inbox";

import AddNewProperty from "./Property/AddNewProperty/AddNewProperty";
import EditProperty from "./Property/EditProperty/EditProperty";

import "./assets/css/style.min.css";
import "./assets/css/dashboard1.css";

import useWindowSize from "./shared/hooks/useWindowSizes";

export const prod = process.env.NODE_ENV === "production" ? true : false;
export const link = prod ? "" : "http://localhost:3000";

function App() {
  const size = useWindowSize();

  return (
    <Router history={history}>
      <div
        id="main-wrapper"
        className={size.width < 1170 ? "mini-sidebar" : ""}
      >
        <Topbar />
        <Navigation />
        <div className="page-wrapper">
          <Switch>
            <Route
              exact
              path={prod ? "/dashboard" : "/"}
              component={Dashboard}
            />
            <Route
              exact
              path={prod ? "/dashboard/inbox" : "/inbox"}
              component={Inbox}
            />
            <Route
              exact
              path={prod ? "/dashboard/add-new-property" : "/add-new-property"}
              component={AddNewProperty}
            />
            <Route
              exact
              path={prod ? "/dashboard/edit/:pId" : "/edit/:pId"}
              render={(routeProps) => <EditProperty {...routeProps} />}
            />
          </Switch>
        </div>
      </div>
    </Router>
  );
}

export default App;
