import React from "react";

import userImage from "../../../assets/images/users/2.jpg";
import { link } from "../../../App";

const ProfileBox = () => {
  return (
    <div className="user-profile">
      <div className="user-pro-body">
        <div>
          <img
            src={`${link}/img/users/ayse.jpg`}
            alt="user-img"
            className="img-circle"
          />
        </div>
        <div className="text-center">Ayse Rehber</div>
      </div>
    </div>
  );
};

export default ProfileBox;
