import React from "react";
//Help to decide form wizads steps classname
const StepRenderHelper = ({ stepsContent, activeStep, handleStep }) => {
  const handleHelper = step => {
    if (Number(step) < activeStep) {
      handleStep(Number(step));
    }
  };
  return (
    <>
      {Object.keys(stepsContent).map(step => {
        let tempClassName = "";
        if (Number(step) - 1 === 0) {
          tempClassName = "first";
        } else if (Number(step) === Object.keys(stepsContent).length) {
          tempClassName = "last";
        }
        if (Number(step) === activeStep) {
          tempClassName += " current";
        } else {
          tempClassName += " disabled";
        }
        if (Number(step) < activeStep) {
          tempClassName = "done";
        }
        return (
          <li
            key={step}
            role="tab"
            className={tempClassName}
            aria-disabled={activeStep === Number(step) ? "false" : "false"}
            aria-selected={activeStep === Number(step) ? "true" : "false"}
          >
            <a href="#" onClick={() => handleHelper(step)}>
              <span className="step" wfd-id="102">
                {step}
              </span>
              Step {step}
            </a>
          </li>
        );
      })}
    </>
  );
};

export default StepRenderHelper;
