import React, { useState, useEffect } from "react";
import { link } from "../../../App";

const InfoCards = () => {
  const [numberState, setNumberState] = useState({ all: 0, rent: 0, sale: 0 });

  useEffect(() => {
    fetch(`${link}/api/v1/properties/countRentSale`)
      .then((res) => res.json())
      .then((json) => {
        if (json.status === "success") {
          setNumberState({ all: json.all, rent: json.rent, sale: json.sale });
        }
      });
  }, []);

  return (
    <>
      <div className="col-lg-4 col-sm-12 col-xs-12">
        <div className="card">
          <div className="card-body">
            <h5 className="card-title text-uppercase">All Properties</h5>
            <div className="d-flex align-items-center no-block m-t-20 m-b-10">
              <h1>
                <i className="ti-home text-info"></i>
              </h1>
              <div className="ml-auto">
                <h1 className="text-muted">{numberState.all}</h1>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="col-lg-4 col-sm-6 col-xs-12">
        <div className="card">
          <div className="card-body">
            <h5 className="card-title text-uppercase">Properties for Sale</h5>
            <div className="d-flex align-items-center no-block m-t-20 m-b-10">
              <h1>
                <i className="icon-tag text-purple"></i>
              </h1>
              <div className="ml-auto">
                <h1 className="text-muted">{numberState.sale}</h1>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="col-lg-4 col-sm-6 col-xs-12">
        <div className="card">
          <div className="card-body">
            <h5 className="card-title text-uppercase">Properties for Rent</h5>
            <div className="d-flex align-items-center no-block m-t-20 m-b-10">
              <h1>
                <i className="icon-basket text-danger"></i>
              </h1>
              <div className="ml-auto">
                <h1 className="text-muted">{numberState.rent}</h1>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default InfoCards;
