import React, { useState } from "react";

import Header from "./components/Header";

import StepRenderHelper from "./functions/StepRenderHelper";
import FormElements from "./components/FormElements/FormElements";
import Row from "../../shared/html/Row";
import Col from "../../shared/html/Col";

import "./styles/steps.css";
import "./styles/icheck.css";
import "./styles/dropify.min.css";
import Loading from "../../shared/loading/Loading";
import history from "../../shared/history/history";
import { link } from "../../App";

const AddNewProperty = () => {
  const [loadingIcon, setLoadingIcon] = useState(false);

  const [activeStep, setActiveStep] = useState(1);
  const [formState, setFormState] = useState({
    title: "",
    basic: {
      status: "rent",
      propertyType: "",
      price: "",
      sqft: "",
      bedrooms: "",
      bathrooms: "",
    },
    location: {
      address: "",
      city: "",
      state: "",
      zipCode: "",
    },
    detailedInfo: {
      description: "",
      buildingAge: "",
      garage: "",
      rooms: "",
      otherFeatures: {
        internet: false,
        beach: false,
        balcony: false,
        airCondition: false,
        terrace: false,
        heating: false,
        wifi: false,
        parking: false,
        icon: false,
        smokingAllow: false,
        microwave: false,
        bedding: false,
      },
    },
  });

  const [imageState, setImageState] = useState({
    mainImage: "",
    gallery: [],
  });

  const handleInputChange = (field, value, group) => {
    let tempObj = { ...formState };

    if (group !== "" && group !== "otherFeatures") {
      tempObj[group][field] = value;
    } else if (group === "otherFeatures") {
      tempObj.detailedInfo.otherFeatures[field] = value;
    } else {
      tempObj[field] = value;
    }
    console.error("FORM FIELDS", tempObj);
    setFormState(tempObj);
  };

  const handleFileChange = (file, group) => {
    let tempObj = { ...imageState };

    if (group === "mainImage") {
      tempObj[group] = file;
    } else {
      tempObj[group] = [...file];
    }
    console.error("UPLOADS", tempObj);
    setImageState(tempObj);
  };
  const [errorStop, setErrorStop] = useState(false);
  const handleFormSubmit = () => {
    fetch(`${link}/api/v1/properties`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(formState),
    })
      .then((res) => res.json())
      .then((json) => {
        let fetchCondition = false;
        let formData = new FormData();
        if (json.status === "success") {
          if (imageState.mainImage !== "") {
            formData.append("mainImage", imageState.mainImage);
            fetchCondition = true;
          }
          if (imageState.gallery.length > 0) {
            imageState.gallery.map((image) => {
              formData.append("gallery", image);
            });
            fetchCondition = true;
          }
        } else if (json.status === "error") {
          setErrorStop(true);
          throw Error(JSON.stringify(json.error));
        }
        return { fetchCondition, formData, json };
      })
      .then((pro) => {
        if (pro.fetchCondition) {
          fetch(`${link}/api/v1/properties/${pro.json.data.data._id}`, {
            method: "PATCH",
            body: pro.formData,
          })
            .then((res) => res.json())
            .then((json) => {
              if (json.status === "error") {
                setErrorStop(true);
                throw Error(JSON.stringify(json.error));
              }
            })
            .catch((err) => {
              console.error(err);
            });
        }
      })
      .then(() => {
        if (!errorStop) {
          history.push("/");
        }
      })
      .catch((err) => console.error(err));
  };

  const stepsContent = {
    "1": (
      <section>
        <Row>
          <Col md={6}>
            <FormElements
              req
              label="Title"
              name="title"
              handleChange={handleInputChange}
              state={formState.title}
            />
          </Col>
        </Row>
        <Row>
          <Col md={12}>
            <h3 className="box-title">Basics</h3>
            <hr className="m-t-0 m-b-40" style={{ width: "100%" }} />
          </Col>
        </Row>
        <Row>
          <Col md={6}>
            <FormElements
              req
              label="Status"
              type="dropdown"
              options={["rent", "sale"]}
              name="status"
              handleChange={handleInputChange}
              state={formState.basic.status}
              group="basic"
            />
          </Col>
          <Col md={6}>
            <FormElements
              label="Property Type"
              type="dropdown"
              options={[
                "",
                "Houses",
                "Apartment",
                "Villas",
                "Commercial",
                "Offices",
              ]}
              name="propertyType"
              handleChange={handleInputChange}
              state={formState.basic.propertyType}
              group="basic"
            />
          </Col>
        </Row>
        <Row>
          <Col md={6}>
            <FormElements
              label="Price"
              inputType="number"
              name="price"
              handleChange={handleInputChange}
              state={formState.basic.price}
              group="basic"
            />
          </Col>
          <Col md={6}>
            <FormElements
              label="Sqft"
              inputType="number"
              name="sqft"
              handleChange={handleInputChange}
              state={formState.basic.sqft}
              group="basic"
            />
          </Col>
        </Row>
        <Row>
          <Col md={6}>
            <FormElements
              label="Bedrooms"
              inputType="number"
              name="bedrooms"
              handleChange={handleInputChange}
              state={formState.basic.bedrooms}
              group="basic"
            />
          </Col>
          <Col md={6}>
            <FormElements
              label="Bathrooms"
              inputType="number"
              name="bathrooms"
              handleChange={handleInputChange}
              state={formState.basic.bathrooms}
              group="basic"
            />
          </Col>
        </Row>
      </section>
    ),
    "2": (
      <section>
        <Row>
          <Col md={6}>
            <div class="card">
              <div class="card-body">
                <h4 class="card-title">File Upload1</h4>
                <label for="mainImage">
                  Your so fresh input file — Default version
                </label>
                <input
                  type="file"
                  id="mainImage"
                  class="dropify"
                  accept="image/*"
                  onChange={(e) =>
                    handleFileChange(e.target.files[0], "mainImage")
                  }
                />
              </div>
            </div>
          </Col>
          <Col md={6}>
            <div class="card">
              <div class="card-body">
                <h4 class="card-title">File Upload1</h4>
                <label for="gallery">
                  Your so fresh input file — Default version
                </label>
                <input
                  type="file"
                  id="gallery"
                  class="dropify"
                  multiple
                  accept="image/*"
                  onChange={(e) => handleFileChange(e.target.files, "gallery")}
                />
              </div>
            </div>
          </Col>
        </Row>
      </section>
    ),
    "3": (
      <section>
        <Row>
          <Col md={12}>
            <h3 className="box-title">Location</h3>
            <hr className="m-t-0 m-b-40" style={{ width: "100%" }} />
          </Col>
        </Row>
        <Row>
          <Col md={6}>
            <FormElements
              req
              label="Address"
              name="address"
              handleChange={handleInputChange}
              state={formState.location.address}
              group="location"
            />
          </Col>
          <Col md={6}>
            <FormElements
              label="City"
              name="city"
              handleChange={handleInputChange}
              state={formState.location.city}
              group="location"
            />
          </Col>
        </Row>
        <Row>
          <Col md={6}>
            <FormElements
              label="State"
              name="state"
              handleChange={handleInputChange}
              state={formState.location.state}
              group="location"
            />
          </Col>
          <Col md={6}>
            <FormElements
              label="Zip Code"
              name="zipCode"
              handleChange={handleInputChange}
              state={formState.location.zipCode}
              group="location"
            />
          </Col>
        </Row>
      </section>
    ),
    "4": (
      <section>
        <Row>
          <Col md={12}>
            <h3 className="box-title">Detailed Info</h3>
            <hr className="m-t-0 m-b-40" style={{ width: "100%" }} />
          </Col>
        </Row>
        <Row>
          <Col md={12}>
            <FormElements
              label="Description"
              name="description"
              handleChange={handleInputChange}
              state={formState.detailedInfo.description}
              group="detailedInfo"
            />
          </Col>
        </Row>
        <Row>
          <Col md={4}>
            <FormElements
              label="Building Age"
              name="buildingAge"
              inputType="number"
              handleChange={handleInputChange}
              state={formState.detailedInfo.buildingAge}
              group="detailedInfo"
            />
          </Col>
          <Col md={4}>
            <FormElements
              label="Garage"
              name="garage"
              inputType="number"
              handleChange={handleInputChange}
              state={formState.detailedInfo.garage}
              group="detailedInfo"
            />
          </Col>
          <Col md={4}>
            <FormElements
              label="Rooms"
              name="rooms"
              inputType="number"
              handleChange={handleInputChange}
              state={formState.detailedInfo.rooms}
              group="detailedInfo"
            />
          </Col>
        </Row>
        <Row>
          <Col md={12}>
            <h3 className="box-title">Other Features</h3>
            <hr className="m-t-0 m-b-40" style={{ width: "100%" }} />
          </Col>
        </Row>
        <Row>
          {Object.keys(formState.detailedInfo.otherFeatures).map((feature) => {
            return (
              <Col md={3} key={feature}>
                <FormElements
                  type="checkbox"
                  name={feature}
                  state={formState.detailedInfo.otherFeatures[feature]}
                  group={"otherFeatures"}
                  handleChange={handleInputChange}
                  label={feature}
                />
              </Col>
            );
          })}
        </Row>
      </section>
    ),
  };

  const handleStep = (step) => {
    setActiveStep(step);
  };

  return (
    <div className="container-fluid">
      <Header />

      <div className="row" id="validation">
        <div className="col-12">
          <div className="card wizard-content">
            <div className="card-body">
              <h4 className="card-title">Step wizard with validation</h4>
              <h6 className="card-subtitle">
                You can us the validation like what we did
              </h6>
              <form
                action="#"
                className="validation-wizard wizard-circle wizard clearfix"
              >
                <div className="steps clearfix">
                  <ul role="tablist">
                    <StepRenderHelper
                      stepsContent={stepsContent}
                      activeStep={activeStep}
                      handleStep={handleStep}
                    />
                  </ul>
                </div>
                <div className="content clearfix">
                  <h6 className="title current">Step {activeStep}</h6>
                  {stepsContent[activeStep]}
                </div>
                <div className="actions clearfix">
                  <ul role="menu" aria-label="Pagination">
                    <li
                      className=""
                      style={
                        activeStep === Number(Object.keys(stepsContent)[0])
                          ? { display: "none" }
                          : { display: "unset" }
                      }
                    >
                      <span>
                        <button
                          type="button"
                          className="btn btn-info m-l-15"
                          onClick={() => handleStep(activeStep - 1)}
                          role="menuitem"
                        >
                          Previous
                        </button>
                      </span>
                    </li>
                    <li
                      aria-hidden="false"
                      style={
                        activeStep !== Object.keys(stepsContent).length
                          ? { display: "unset" }
                          : { display: "none" }
                      }
                    >
                      <button
                        type="button"
                        className="btn btn-info m-l-15"
                        onClick={() => handleStep(activeStep + 1)}
                        role="menuitem"
                      >
                        Next
                      </button>
                    </li>
                    <li
                      aria-hidden="true"
                      style={
                        activeStep === Object.keys(stepsContent).length
                          ? { display: "unset" }
                          : { display: "none" }
                      }
                    >
                      <button
                        type="button"
                        className="btn btn-info m-l-15"
                        onClick={() => handleFormSubmit()}
                        role="menuitem"
                      >
                        Submit
                      </button>
                      {loadingIcon ? <Loading /> : null}
                    </li>
                  </ul>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default AddNewProperty;
